body {
  margin: 0;
  padding: 0;
  color: #555;
  font-family: 'Roboto', 'Helvetica Neue', sans-serif;
  width: 100%; }
  body .background-image {
    display: none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    background: url("/tulips.jpg");
    background-size: 100%;
    -webkit-filter: opacity(30%) saturate(50%) blur(5px);
            filter: opacity(30%) saturate(50%) blur(5px); }
  body .content {
    max-width: 1024px;
    margin: 18px auto;
    background: white;
    padding: 12px 24px; }

header {
  text-align: center;
  background: white;
  padding: 12px; }
  header svg {
    width: 611px;
    max-width: 100%;
    margin: 16px 0 36px 0; }
  header a {
    color: #555;
    text-transform: uppercase;
    text-decoration: none;
    margin: 0 12px;
    display: inline-block; }
    header a:after {
      height: 2px;
      background: #00bcd4;
      display: block;
      content: '';
      width: 0;
      margin: 0px auto;
      position: relative;
      bottom: -2px; }
    header a:hover {
      color: #444;
      -webkit-transition: color 0.5s;
      -o-transition: color 0.5s;
      transition: color 0.5s; }
    header a:hover:after {
      width: 100%;
      -webkit-transition: width 0.5s;
      -o-transition: width 0.5s;
      transition: width 0.5s; }
    header a.active {
      border-bottom: 2px solid; }

.wish-list {
  overflow: hidden; }
  .wish-list a {
    float: left;
    display: block;
    width: 200px;
    height: 280px;
    margin: 28px;
    color: #777;
    text-decoration: none; }
    @media screen and (max-width: 800px) {
      .wish-list a {
        width: 100%;
        height: auto;
        margin: 32px 0; } }
  .wish-list h3 {
    margin: 12px 0 0;
    font-size: 14px;
    text-align: center; }

article.wish {
  overflow: hidden; }
  article.wish img.product-image {
    float: left;
    max-width: 500px;
    max-height: 500px; }
    @media screen and (max-width: 800px) {
      article.wish img.product-image {
        float: none;
        max-width: 100%;
        max-height: 100%; } }
  article.wish .details-container {
    float: left;
    max-width: 560px;
    margin-left: 72px; }
    @media screen and (max-width: 800px) {
      article.wish .details-container {
        float: none;
        width: 100%;
        margin: 28px 0; } }
  article.wish .other-gifts {
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 42px 52px; }
    @media screen and (max-width: 800px) {
      article.wish .other-gifts {
        display: none; } }
  article.wish .pledge-container {
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 42px 52px; }
    @media screen and (max-width: 800px) {
      article.wish .pledge-container {
        position: relative; } }
    article.wish .pledge-container button svg {
      height: 40px;
      float: left;
      fill: white;
      margin: 2px 0 0 4px; }
    article.wish .pledge-container a.button-pledge {
      display: inline-block;
      margin: 12px 0 0 0;
      padding: 9px 16px;
      background: #43a3ac;
      color: white;
      text-decoration: none; }

.price {
  font-size: 19px;
  margin: 0 0 12px 0; }

.quantity {
  margin: 0 18px; }

.bottom-sheet {
  position: fixed;
  bottom: 0;
  margin: 100px 0 0;
  background: white;
  left: 0;
  right: 0;
  border-top: 1px solid #ccc;
  -webkit-box-shadow: 0px -1px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 0px -1px 3px rgba(0, 0, 0, 0.1);
  -webkit-transition-property: all;
  -o-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: .5s;
       -o-transition-duration: .5s;
          transition-duration: .5s;
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
       -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
          transition-timing-function: cubic-bezier(0, 1, 0.5, 1); }
  .bottom-sheet > svg {
    height: 85px;
    position: absolute;
    top: -42px;
    float: right;
    right: 50px;
    background: white;
    border-radius: 120px;
    padding: 7px;
    fill: #00bcd4;
    -webkit-box-shadow: 0px -3px 3px rgba(0, 0, 0, 0.1);
            box-shadow: 0px -3px 3px rgba(0, 0, 0, 0.1); }
  .bottom-sheet .third {
    float: left;
    width: 30%;
    padding: 18px; }
    @media screen and (max-width: 800px) {
      .bottom-sheet .third {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        float: none;
        width: 100%; } }

.bottom-sheet-overlay {
  background: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  -webkit-transition-property: all;
  -o-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: .5s;
       -o-transition-duration: .5s;
          transition-duration: .5s;
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
       -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
          transition-timing-function: cubic-bezier(0, 1, 0.5, 1); }

.login-page {
  background: url(/us.jpg) no-repeat center center fixed;
  background-size: cover; }
  .login-page form {
    max-width: 257px;
    background: rgba(255, 255, 255, 0.5);
    padding: 18px;
    margin: 12px; }
  .login-page svg.logo {
    fill: white;
    position: fixed;
    bottom: 20px;
    right: 20px; }

.gallery {
  text-align: center;
  margin: 32px 0; }
  .gallery img {
    max-width: 200px;
    margin-right: 4px; }

dl dt, dl dd {
  margin: 0;
  padding: 0;
  -webkit-margin-start: 0;
  -moz-margin-start: 0; }

dl dt {
  font-weight: bold;
  margin: 12px 0 0; }

dl dd {
  padding: 6px 0; }
  dl dd p {
    margin: 0 0 6px 0; }

.event-main-image {
  max-width: 500px;
  float: left;
  margin: 0 24px 24px 0;
  border-bottom: 5px solid #00bcd4; }
  @media screen and (max-width: 800px) {
    .event-main-image {
      float: none;
      max-width: 100%;
      margin: 0 0 24px 0; } }

.transition-enter .bottom-sheet-overlay,
.transition-exit.transition-exit-active .bottom-sheet-overlay {
  opacity: 0; }

.transition-enter.transition-enter-active .bottom-sheet-overlay,
.transition-exit .bottom-sheet-overlay {
  opacity: 1; }

.transition-enter .bottom-sheet,
.transition-exit.transition-exit-active .bottom-sheet {
  bottom: -300px; }

.transition-enter-active .bottom-sheet,
.transition-exit .bottom-sheet {
  bottom: 0; }

.cancel-progress {
  width: 300px;
  background: rgba(255, 255, 255, 0.5);
  padding: 12px;
  color: #222;
  margin: 32px; }



